export default {
   data () {
         return {
               showToast:false
         }
   },
   methods :{
         back () {
               this.$router.back(-1);
         },
         toInvite () {
             this.showToast=true;
         },
         toInviteRule () {
             this.$router.push({path:'/inviterule'});   
         }
   } 
}



